@import url(https://fonts.googleapis.com/css2?family=Fredoka&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

h2 {
    font-weight: bold;
    width: auto;
    text-align: center;
}

.nav {
    background-color: rgb(248, 206, 236);
    border-bottom: 3px solid ghostwhite;
    padding: 0.5em;
    height: 70px;
    width: 100%;
    justify-content: center;
    position:fixed;
    top: 0px;
    left: 0px;
}
h3 {
    color: #12050e;
    font-size: 40px;
    text-align: center;
}

hr {
    margin-right: 5px;
    color: #12050e;
}

.card {
    margin-top: 1.5em;
    -webkit-animation: fadeIn 1s ease-in-out;
            animation: fadeIn 1s ease-in-out;
    border: 3px solid #12050e;
    border-radius: 8px;
    background-color: #cca7c3;
}
@-webkit-keyframes fadeIn {
    0% {opacity:0;}
    100% {opacity:1;}
}
@keyframes fadeIn {
    0% {opacity:0;}
    100% {opacity:1;}
}

.teamIcon {
    margin-top: 8px;
    border: 3px solid #12050e;
    border-radius: 8px;
}

.resultIcon {
    height: 20vh;
    border: 3px solid #12050e;
    border-radius: 8px;
    margin: 8px;
    float: left;
}

.display {
    width: 50vh;
    display: block;
    margin-left: auto;
    margin-right: auto;
    margin-top: 1.5em;
    background-color: #cca7c3;
    padding: 8px;
    padding-bottom: 2rem;
    border: 3px solid #12050e;
    border-radius: 8px;
    -webkit-animation: fadeIn 1s ease-in-out;
            animation: fadeIn 1s ease-in-out;
}
@keyframes fadeIn {
    0% {opacity:0;}
    100% {opacity:1;}
}

.roleColor {
    padding: 0.3em;
    border: 0.16em solid rgba(255,0,255, 0);
    border-radius: 0.5em;
    box-sizing: border-box;
    font-weight: 600;
    color: whitesmoke;
}
.roles {
    padding: 0.6em 1.2em;
    margin: 0.2em;
    border: 0.16em solid rgba(255,0,255, 0);
    border-radius: 1em;
    box-sizing: border-box;
    font-weight: 600;
    color: ghostwhite;
}

.roles:hover {
    border: 0.16em solid ghostwhite;
}

.bigBadButton {
    font-size: 30px;
    background-color: pink;
    color: #0a090a;
}
.App {
    background-color: #fae2ea;
    height: 100vh;
    padding-top: 15px;
    box-sizing: border-box;
    font-family: 'Fredoka';
}
